
import Observer from "./Observer";
export namespace  org.unirail
{
    export class Channel {
        session_id = 0;
        client_id = 0;
        param1 = 0;
        param2 = 0;
        longs: number[] | undefined;
        info: Object | undefined;
    }
    export namespace Channel {
        export class  Realm {
            new_Project_Channel : () => Observer.Project.Channel = () => new Observer.Project.Channel()
            new_Project_Host_Port_Pack_Field : () => Observer.Project.Host.Port.Pack.Field = () => new Observer.Project.Host.Port.Pack.Field()
            new_Project_Host : () => Observer.Project.Host = () => new Observer.Project.Host()
            new_Layout_View_Info : () => Observer.Layout.View.Info.Driver = () => Observer.Layout.View.Info.Driver.ONE
            new_Layout : () => Observer.Layout = () => new Observer.Layout()
            new_Project_Host_Port_Pack : () => Observer.Project.Host.Port.Pack = () => new Observer.Project.Host.Port.Pack()
            new_Project_Host_Port : () => Observer.Project.Host.Port = () => new Observer.Project.Host.Port()
            new_Project : () => Observer.Project = () => new Observer.Project()
            new_ObserverToAgent_Show_Code : () => Observer.ObserverToAgent.Show_Code = () => new Observer.ObserverToAgent.Show_Code()
            new_ObserverToAgent_Up_to_date : () => Observer.ObserverToAgent.Up_to_date = () => new Observer.ObserverToAgent.Up_to_date()
            new_Layout_View : () => Observer.Layout.View = () => new Observer.Layout.View()

            CopyTo(dst: Realm): Realm{
                dst.new_Project_Channel = this.new_Project_Channel;
                dst.new_Project_Host_Port_Pack_Field = this.new_Project_Host_Port_Pack_Field;
                dst.new_Project_Host = this.new_Project_Host;
                dst.new_Layout_View_Info = this.new_Layout_View_Info;
                dst.new_Layout = this.new_Layout;
                dst.new_Project_Host_Port_Pack = this.new_Project_Host_Port_Pack;
                dst.new_Project_Host_Port = this.new_Project_Host_Port;
                dst.new_Project = this.new_Project;
                dst.new_ObserverToAgent_Show_Code = this.new_ObserverToAgent_Show_Code;
                dst.new_ObserverToAgent_Up_to_date = this.new_ObserverToAgent_Up_to_date;
                dst.new_Layout_View = this.new_Layout_View;

                return dst;
            }
        }
        export namespace Realm {
            export function Clone(): Realm {return <Realm> Object.assign({}, Realm.DEFAULT);}
            export const  DEFAULT = new Realm();
        }
    }
}
import Channel = org.unirail.Channel
                 export default Channel;
