function show_error_banner(banner: string) {
	const div     = document.getElementById('error_banner')
	div.innerHTML = banner
	
	div.classList.remove("FadeOut")
	div.classList.add("FadeIn")
}

function hide_error_banner() {
	const div = document.getElementById('error_banner')
	
	div.classList.remove("FadeIn");
	div.classList.add("FadeOut")
}

let last_popup: HTMLElement | null
//popup control to show some embedded html in separate place
// for example somewhere in the HTML
//
//<input id = "search_input"
//        onfocusout = "this.value=''; this.style.display = 'none';"
//        oninput = "this.style.width = 5 + this.value.length + 'ch';"
//        type = "search"
//        list = "search_input_list"
//        placeholder = "search"
//        style = "font-size:20px; border:2px solid black; position: absolute; z-index: 1; display: none; "/>
//
// then get reference to `search_input`
//const editor  = document.getElementById('search_input') as HTMLInputElement
// and then call ... provide browser coordinate
// popup(event.clientX, event.clientY, editor)
function popup(popupX: number, popupY: number, popup: HTMLElement | null) {
	if (last_popup && last_popup !== popup) {
		
		popup.style.zIndex       = "auto"
		last_popup.style.display = 'none'
	}
	
	last_popup = popup
	if (popup == null) return
	
	const border = (window.outerWidth - window.innerWidth) / 2
	//https://user-images.githubusercontent.com/29354319/176393772-fd77dfd7-2b8d-43f4-9c53-a87889ac869b.png
	const x0     = (window.screenLeft ?? window.screenX) + border
	let x1       = x0
	const y0     = (window.screenTop ?? window.screenY) + (window.outerHeight - (window.innerHeight + border));
	let y1       = y0
	
	let x2 = x1 + document.documentElement.clientWidth - 2;
	let y2 = y1 + document.documentElement.clientHeight - 2;
	
	const screen: any = window.screen
	x1                = Math.max(screen.availLeft, x1)
	x2                = Math.min(screen.availLeft + screen.availWidth, x2)
	y1                = Math.max(screen.availTop, y1)
	y2                = Math.min(screen.availTop + screen.availHeight, y2)
	
	x1 -= x0 //move to browser coordinate
	x2 -= x0
	y1 -= y0
	y2 -= y0
	
	popup.style.position  = "fixed"
	popup.style.transform = ''
	popup.style.zIndex    = Number.MAX_SAFE_INTEGER.toString() //topmost
	
	popup.tabIndex = 0 //for fire keydown event on div
	popup.addEventListener('keydown',
	                       (event: KeyboardEvent) => { //               close on Escape keypress
		                       if (event.code != "Escape" || !popup) return;
		
		
		                       popup.style.display = 'none'
		                       popup.style.zIndex  = "auto"
		                       popup.removeEventListener('keydown', this);
		                       popup = null
	                       })
	
	const {width, height} = popup.getBoundingClientRect()
	
	const W = x2 - x1
	
	if (W < width) {
		popup.style.transform += ` scaleX(${W / width})`
		
		popupX = -popup.getBoundingClientRect().x
	}
	else if (x1 + W / 2 < popupX)//from center far-right
	{
		if (x2 < popupX + width) popupX = x2 - width
	}
	else popupX = Math.max(x1, popupX)
	
	popup.style.left = popupX + 'px'
	
	const H = y2 - y1
	if (H < height) {
		popup.style.transform += ` scaleY(${H / height})`
		popupY = -popup.getBoundingClientRect().y
	}
	else if (y1 + (H) / 2 < popupY) //from center far-bottom
	{
		if (y2 < popupY + height) popupY = y2 - height
	}
	else popupY = Math.max(y1, popupY)
	
	popup.style.top = popupY + 'px'
	
	popup.style.display = 'block' //make visible
	
	popup.focus()
}



